export function getLocalizedString(date: Date, options?: Intl.DateTimeFormatOptions) {
  return date.toLocaleString(undefined, {
    hour12: false,
    timeZoneName: 'short',
    day: '2-digit',
    month: '2-digit',
    year: 'numeric',
    hour: '2-digit',
    minute: '2-digit',
    second: '2-digit',
    weekday: 'long',
    ...options,
  });
}
